<template>
  <div class="ranking-box" :style="{backgroundImage: 'url('+bgImg+')'}">
    <div class="ranking-bar">
      <van-nav-bar
        :title="$t('tradeRank')"
        left-arrow
        @click-left="linkHome"
        @click-right="linkSearch"
      >
        <img class="bar-img" slot="right" src="../../assets/homeIcon/RKsearch.png" height="60" width="60"/>
      </van-nav-bar>
    </div>
    <div class="my">
      <div>
        <div>{{$t('win')}}</div>
        <div>{{winRate}}%</div>
      </div>
      <div>
        <div>
          <img class="my-img" src="../../assets/hcLogo.png"/>
        </div>
        <div>{{account}}</div>
      </div>
      <div>
        <div>{{$t('tradeNum')}}</div>
        <div>{{sumCount}}</div>
      </div>
    </div>
    <div class="box">
      <div class="RK-bottom-box">
        <div class="RK-bottom">
          <van-tabs v-model="active" color="#1988FF" animated swipeable title-active-color="#1988FF" title-inactive-color="#222222" background="#fff"  @change="changeTab">
            <van-tab :title="$t('monthR')">
              <div class="content1">
                <div>
                  <div>{{$t('Rank')}}</div>
                  <div>{{$t('account')}}</div>
                  <div>{{$t('win')}}</div>
                  <div>{{$t('Number')}}</div>
                  <div></div>
                </div>
            </div>
              <div class="content" v-if="!showClass">
                <div>
                  <div class="target-box">
                    <div class="target">{{$t('target')}}</div>
                  </div>
                  <div>{{'***'+(followAccount).substring(followAccount.length-3)}}</div>
                  <div>{{followWinRate}}%</div>
                  <div>{{followSumCount}}</div>
                  <div class="noFollow1" @click="linkNoFollow()" >{{$t('EOOrder')}}</div>
                </div>
            </div>
              <div class="content">
                <div v-for="(item,index) in monthList">
                  <div class="img-box" v-if="index+1 === 1">
                    <img src="../../assets/homeIcon/first.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 2">
                    <img src="../../assets/homeIcon/Second.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 3">
                    <img src="../../assets/homeIcon/Third.png" class="content-img"/>
                  </div>
                  <div v-else>{{index+1}}</div>
                  <div>{{'***'+(item.account).substring(item.account.length-3)}}</div>
                  <div>
                    <span v-if="item.winRate" style="color: #FD4F50">{{item.winRateShow}}</span>
                    <span v-else style="color: #07B76F">{{item.lostRateShow}}</span>
                  </div>
                  <div>{{item.sumCount}}</div>
                  <div class="noFollow" @click="linkFollow(index,'monthList')" :style="{backgroundColor:showClass?'#1988FF':'#999'}">
                    {{$t('follow')}}</div>
                </div>
              </div>
            </van-tab>
            <van-tab :title="$t('week')">
              <div class="content1">
                <p class="quan"></p>
                <p class="quan"></p>
                <div>
                  <div>{{$t('Rank')}}</div>
                  <div>{{$t('account')}}</div>
                  <div>{{$t('win')}}</div>
                  <div>{{$t('Number')}}</div>
                  <div></div>
                </div>
              </div>
              <div class="content">
                <div v-for="(item,index) in weekList">
                  <div class="img-box" v-if="index+1 === 1">
                    <img src="../../assets/homeIcon/first.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 2">
                    <img src="../../assets/homeIcon/Second.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 3">
                    <img src="../../assets/homeIcon/Third.png" class="content-img"/>
                  </div>
                  <div v-else>{{index+1}}</div>
                  <div>{{'***'+(item.account).substring(item.account.length-3)}}</div>
                  <div>
                    <span v-if="item.winRate" style="color: #FD4F50">{{item.winRateShow}}</span>
                    <span v-else style="color: #07B76F">{{item.lostRateShow}}</span>
                  </div>
                  <div>{{item.sumCount}}</div>
                  <div class="noFollow" @click="linkFollow(index,'weekList')" :style="{backgroundColor:showClass?'#1988FF':'#999'}">{{$t('follow')}}</div>
                </div>
              </div>
            </van-tab>
            <van-tab :title="$t('day')">
              <div class="content1">
                <p class="quan"></p>
                <p class="quan"></p>
                <div>
                  <div>{{$t('Rank')}}</div>
                  <div>{{$t('account')}}</div>
                  <div>{{$t('win')}}</div>
                  <div>{{$t('Number')}}</div>
                  <div></div>
                </div>
              </div>
              <div class="content">
                <div v-for="(item,index) in dayList">
                  <div class="img-box" v-if="index+1 === 1">
                    <img src="../../assets/homeIcon/first.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 2">
                    <img src="../../assets/homeIcon/Second.png" class="content-img"/>
                  </div>
                  <div class="img-box" v-else-if="index+1 === 3">
                    <img src="../../assets/homeIcon/Third.png" class="content-img"/>
                  </div>
                  <div v-else>{{index+1}}</div>
                  <div>{{'***'+(item.account).substring(item.account.length-3)}}</div>
                  <div>
                    <span v-if="item.winRate" style="color: #FD4F50">{{item.winRateShow}}</span>
                    <span v-else style="color: #07B76F">{{item.lostRateShow}}</span>
                  </div>
                  <div>{{item.sumCount}}</div>
                  <div class="noFollow" @click="linkFollow(index,'dayList')" :style="{backgroundColor:showClass?'#1988FF':'#999'}">{{$t('follow')}}</div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
        <div class="RK-bottom-right" :class="$i18n.locale === 'en'?'RK-bottom-right-en':''"  @click="changeAround">
          <div class="RK-bottom-title" :class="$i18n.locale === 'en'?'RK-bottom-right-en':''">{{around === '前十名'?$t('topTen'):$t('bottomTen')}}</div>
          <div class="arrow1" v-if="aroundOff"></div>
          <div class="arrow" v-else></div>
        </div>
      </div>
    </div>
    <van-dialog
      v-model="showNoFollow"
      :title="$t('documentary')"
      show-cancel-button
      cancelButtonColor="#8C8B8B" confirmButtonColor="#1988FF"
      class="noFollow"
      @confirm="confirmNoFollow"
    >
    </van-dialog>
    <div class="risk-box">
      <van-dialog
        v-model="riskShow"
        :title="$t('followRisk')"
        show-cancel-button
        cancelButtonColor="#8C8B8B" confirmButtonColor="#1988FF"
        :confirm-button-text="$t('RiskC')"
        :cancel-button-text="$t('cancel')"
        @confirm="openRisk">
        <div slot="default" class="risk-content">
          <div class="risk-book">
            　　{{$t('followRisk1')}}
            <br/>　　{{$t('followRisk2')}}
            <br/>　　{{$t('followRisk3')}}
            <br/>　　{{$t('followRisk4')}}
            <br/>　　{{$t('followRisk5')}}
            <br/>　　{{$t('followRisk6')}}
            <br/>　　{{$t('followRisk7')}}
          </div>
        </div>
      </van-dialog>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  export default {
    name: "index",
    data(){
      return{
        bgImg:require('../../assets/homeIcon/rankingBg.png'),
        bg1Img:require('../../assets/homeIcon/RKbg2.png'),
        active:0,
        sumCount:0,
        winRate:'0',
        account:'',
        aroundOff:true,
        around:'前十名',
        monthList:[],
        weekList:[],
        dayList:[],
        showClass:true,
        followAccount:'',
        followSumCount:0,
        followWinRate:0,
        showNoFollow:false,
        myAccount:'',
        riskShow:false,
        idx : '',
        list :''
      }
    },
    created(){
      this.uid = localStorage.getItem('uid');
      this.discUrl = localStorage.getItem('discUrl')
      this.myAccount = localStorage.getItem('account')
      this.getMyRate()
      this.getWinRanking('month','monthList')
    },
    methods:{
      // 风险提示
      openRisk(){
        // this.riskFollow(this.idx,this.list)
        let that = this
        console.log(that.list)
        switch (that.list) {
          case 'monthList':
            this.$router.push({
              name:'follow',
              params:{
                data:that.monthList[that.idx]
              }
            })
            break;
          case 'weekList':
            this.$router.push({
              name:'follow',
              params:{
                data:that.weekList[that.idx]
              }
            })
            break;
          case 'dayList':
            this.$router.push({
              name:'follow',
              params:{
                data:that.dayList[that.idx]
              }
            })
            break;
        }
      },
      linkHome(){
        this.$router.push({
          name:'home'
        })
      },
      linkSearch(){
        this.$router.push({
          name:'searchRK',
          params:{
            account : this.followAccount
          }
        })
      },
      linkFollow(idx,list){
        this.idx = idx
        this.list = list
        // this.riskShow = true
        let that = this
        let postData = {}
        let data = {}
        data.userId = this.uid
        data.multiple = ''
        data.direction = ''
        postData.type = 0
        if(this.showClass){
          switch (list) {
            case 'monthList':
              data.isFollowedId = this.monthList[idx].userId
              postData.documentary = data
              this.$axios.post('http://'+this.discUrl+':7777/api/documentary/insertDocumentary',postData).then(res =>{
                if(res.data.code === 0){
                  if(this.monthList[idx].account === this.myAccount){
                    Toast({
                      duration:2000,
                      position:'bottom',
                      message:that.$t('noFollow')
                    })
                  }else {
                    this.riskShow = true
                    // this.$router.push({
                    //   name:'follow',
                    //   params:{
                    //     data:this.monthList[idx]
                    //   }
                    // })
                  }
                }else {
                  Toast({
                    duration:2000,
                    position:'bottom',
                    message:res.data.msg
                  })
                }
              })
              break;
            case 'weekList':
              data.isFollowedId = this.weekList[idx].userId
              postData.documentary = data
              this.$axios.post('http://'+this.discUrl+':7777/api/documentary/insertDocumentary',postData).then(res =>{
                if(res.data.code === 0){
                  if(this.weekList[idx].account === this.myAccount){
                    Toast({
                      duration:2000,
                      position:'bottom',
                      message:that.$t('noFollow')
                    })
                  }else {
                    this.riskShow = true
                    // this.$router.push({
                    //   name:'follow',
                    //   params:{
                    //     data:this.weekList[idx]
                    //   }
                    // })
                  }
                }else {
                  Toast({
                    duration:2000,
                    position:'bottom',
                    message:res.data.msg
                  })
                }
              })
              break;
            case 'dayList':
              data.isFollowedId = this.dayList[idx].userId
              postData.documentary = data
              this.$axios.post('http://'+this.discUrl+':7777/api/documentary/insertDocumentary',postData).then(res =>{
                if(res.data.code === 0){
                  if(this.dayList[idx].account === this.myAccount){
                    Toast({
                      duration:2000,
                      position:'bottom',
                      message:that.$t('noFollow')
                    })
                  }else {
                    this.riskShow = true
                    // this.$router.push({
                    //   name:'follow',
                    //   params:{
                    //     data:this.dayList[idx]
                    //   }
                    // })
                  }
                }else {
                  Toast({
                    duration:2000,
                    position:'bottom',
                    message:res.data.msg
                  })
                }
              })
              break;
          }
        }
      },
      riskFollow(idx,list){
      },
      linkNoFollow(){
        this.showNoFollow = true
      },
      changeTab(){
        console.log(this.active)
        switch (this.active) {
          case 0:
            this.getWinRanking('month','monthList')
            this.aroundOff = true
            this.around = '前十名'
            break;
          case 1:
            this.getWinRanking('week','weekList')
            this.aroundOff = true
            this.around = '前十名'
            break;
          case 2:
            this.getWinDataRanking()
            this.aroundOff = true
            this.around = '前十名'
            break;
        }
      },
      changeAround(){
        if(this.aroundOff){
          this.around = '后十名'
        }else {
          this.around = '前十名'
          console.log(this.active)
        }
        switch (this.active) {
          case 0:
            if(this.aroundOff){
              this.getLostRanking('month','monthList')
            }else {
              this.getWinRanking('month','monthList')
            }
            break;
          case 1:
            if(this.aroundOff){
              this.getLostRanking('week','weekList')
            }else {
              this.getWinRanking('week','weekList')
            }
            break;
          case 2:
            if(this.aroundOff){
              this.getLostDataRanking()
            }else {
              this.getWinDataRanking()
            }
            break;
        }
        this.aroundOff = !this.aroundOff
      },
      // 获取当前登录的胜率
      getMyRate(){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/selectWinRateByUserId?userId='+this.uid).then(res =>{
          console.log(res,'个人')
          if(res.data.code === 0){
            this.sumCount = res.data.object.selfMap.sumCount
            this.winRate = res.data.object.selfMap.winRate
            this.account = res.data.object.selfMap.account
            if(res.data.object.followedMap !== null){
              this.showClass = false
              this.followAccount = res.data.object.followedMap.account
              this.followSumCount = res.data.object.followedMap.sumCount
              this.followWinRate = (res.data.object.followedMap.winRate).toFixed(2)
            }else {
              this.showClass = true
            }
          }
        })
      },
      // 取消跟单
      confirmNoFollow(){
        let that = this
        this.$axios.delete('http://'+this.discUrl+':7777/api/documentary/deleteDocumentary?userId='+this.uid).then(res => {
          console.log(res, '取消跟单')
          if(res.data.code === 0){
            this.showClass = true
            Toast({
              duration:2000,
              message:that.$t('noFollowS'),
              position:'bottom'
            })
          }
        })
      },
      // 获取日排行
      // column:winRate(胜率)/lostRate（败率）
      getWinDataRanking(){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/dayWinLostRate?account=&column=winRate').then(res =>{
          if(res.status === 200){
            console.log(res,'日正排行')
            for(let i=0;i<res.data.length;i++){
              res.data[i].winRateShow = (res.data[i].winRate*100).toFixed(2)+'%'
            }
          }
          this.dayList = res.data
          console.log(this.dayList,'this.dayList')
        })
      },
      getLostDataRanking(){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/dayWinLostRate?account=&column=lostRate').then(res =>{
          if(res.status === 200){
            console.log(res,'日正排行')
            for(let i=0;i<res.data.length;i++){
              res.data[i].lostRateShow = (res.data[i].lostRate*100).toFixed(2)+'%'
            }
          }
          this.dayList = res.data
          console.log(this.dayList,'this.dayList')
        })
      },
      // 获取排行
      // period:week(周)/month(月)
      getWinRanking(period,list){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/periodWinLostRate?account=&column=winRate&period='+period).then(res =>{
          console.log(res,'正排行')
          if(res.data.code === 0){
            for(let i=0;i<res.data.object.length;i++){
              res.data.object[i].winRateShow = (res.data.object[i].winRate*100).toFixed(2)+'%'
            }
            if(list === 'weekList'){
              this.weekList = res.data.object
            }
            if(list === 'monthList'){
              this.monthList = res.data.object
            }
          }
        })
      },
      getLostRanking(period,list){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/periodWinLostRate?account=&column=lostRate&period='+period).then(res =>{
          console.log(res,'反排行')
          if(res.data.code === 0){
            for(let i=0;i<res.data.object.length;i++){
              res.data.object[i].lostRateShow = (res.data.object[i].lostRate*100).toFixed(2)+'%'
            }
            if(list === 'weekList'){
              this.weekList = res.data.object
            }
            if(list === 'monthList'){
              this.monthList = res.data.object
            }
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../style/home";
</style>



